import { React, Component } from "react";

class MetaGen extends Component {
	constructor(props) {
		super(props);

		this.settings = {
			type: "meta",
			amount: 6,
			className: "metagen",
		};

		this.metaIni = this.metaIni.bind(this);
		this.gen = this.gen.bind(this);
	}

	gen() {
		let metaarray = [];
		for (let index = 0; index < this.settings.amount; index++) {
			let el = document.createElement(this.settings.type);
			metaarray.push(el);
		}
		metaarray[0].setAttribute("content", "article");
		metaarray[0].setAttribute("property", "og:type");
		metaarray[1].setAttribute("content", this.props.locationLink);
		metaarray[1].setAttribute("property", "og:url");
		metaarray[2].setAttribute("content", "summary");
		metaarray[2].setAttribute("name", "twitter:card");
		metaarray[3].setAttribute("content", "@Artefaktas");
		metaarray[3].setAttribute("name", "twitter:site");
		metaarray[4].setAttribute(
			"content",
			`${window.location.host} | ${this.props.postTitle}`
		);
		metaarray[4].setAttribute("name", "twitter:title");
		metaarray[5].setAttribute("content", this.props.imageLink);
		metaarray[5].setAttribute("property", "og:image");
		metaarray.forEach((element) => {
			element.setAttribute("class", this.settings.className);
			document.head.insertBefore(element, document.head.firstElementChild);
		});
	}

	metaIni() {
		if (document.querySelectorAll(`.${this.settings.className}`).length) {
			let removalArray = document.head.querySelectorAll(
				`.${this.settings.className}`
			);
			removalArray.forEach((element) => {
				document.head.removeChild(element);
			});
			this.gen();
		} else {
			this.gen();
		}
	}

	componentDidMount() {
		this.metaIni();
	}

	render() {
		return <div id="metaupdate" />;
	}
}
export default MetaGen;
